.l-horizontal {
	margin-right: auto;
	margin-left: auto;

	//SP
	&.-spS {
		width: 84%; //375px30px
	}
	&.-spM {
		width: 89.33%; //375px20px
	}
	&.-spL {
		width: 94.66%; //375px10px
	}
	&.-spFull {
		width: 100%;
	}

	//PC
	&.-pcSs {
		max-width: rem(600);
	}
	&.-pcS {
		max-width: rem(800);
	}
	&.-pcSm {
		max-width: rem(900);
	}
	&.-pcM {
		max-width: rem(1000);
	}
	&.-pcLm {
		max-width: rem(1100);
	}
	&.-pcL {
		max-width: rem(1200);
	}

	//FULL 100
	&.-full {
		width: 100%;
	}

	//project
	&.-card {
		width: 100%;
		max-width: rem(1000);

		@include mq() {
			width: 94.66%;
		}
	}

	//
	&.-text {
		width: 84%; //375px30px
		max-width: rem(800);
	}
}

// col margin __________________________________________

//
//l-sectionl-outer

.l-vertical {
	padding-top: rem(64);
	padding-bottom: rem(64);

	@include mq() {
		padding-top: rem(96);
		padding-bottom: rem(96);
	}
}

//
.l-vertical.-container {
	width: 100%;
	max-width: rem(1100);
	margin-right: auto;
	margin-left: auto;
	background-color: #fff;
}

