//
.badge.-row {
	position: relative;
	z-index: 2;
	display: inline-flex;
	align-content: center; //wrap
	align-items: center; //1 
	justify-content: center;
	width: 100%;
	max-width: rem(160);
	height: rem(48);
	margin-bottom: rem(10); //
	text-align: center;
	background-color: $cl-point-change;
	border-radius: 10em 10em 10em 0;

	// 
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		background-color: $cl-point;
		border-radius: 10em 10em 10em 0;
		opacity: 1;
		transition: 200ms;

		//btnBoxhover
		.btnBox:hover & {
			opacity: 0;
		}
	}

	.text {
		position: relative; //zindex
		z-index: 1;
		color: #fff;
	}
}

//SPPC
//hover
.badge.-shortTime {
	position: relative;
	z-index: 2;
	display: inline-flex;
	flex-flow: row wrap;
	align-content: center; //wrap
	align-items: center; //1 
	justify-content: center;
	width: 100%;
	max-width: rem(160);
	height: rem(48);
	color: #fff;
	text-align: center;
	background-color: $cl-point-change;
	border-radius: 10em 10em 10em 0;

	@include mq(lg) {
		width: rem(96);
		height: rem(96);
		border-radius: 10em;
	}

	// 
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		// z-index: 1;
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		background-color: $cl-point;
		border-radius: 10em 10em 10em 0;
		opacity: 1;
		transition: 200ms;

		@include mq(lg) {
			border-radius: 10em;
		}

		//btnBoxhover
		.btnBox:hover & {
			opacity: 0;
		}
	}

	//box
	.box {
		@include lineHeightCrop(1.8); //

		position: relative;
		z-index: 1;
		color: #fff;

		@include mq(lg) {
			flex: 0 0 100%;
		}
	}

	.text {
	}

	//box
	.text.-lg {
		@include mq(lg) {
			font-size: 1.8em;
		}
	}
}

//
//lineHeightCropfontsize.box
//boxsize.text.-small
.badge.-ciecle {
	position: relative;
	display: flex;
	flex-flow: row wrap;
	align-content: center; //wrap
	align-items: center; //1 
	justify-content: center;
	width: rem(100);
	height: rem(100);
	color: #fff;
	text-align: center;

	//box
	.box {
		@include lineHeightCrop(1.8);
	}

	//box
	.box.-textLarge {
		font-size: 2em;
	}

	//box
	.box.-textSmall {
		font-size: 0.8em;
	}

	// 
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		background-color: $cl-point;
		border-radius: 10em;
	}
}

//
.badge.-num {
	@include ff-en();

	display: inline-block;
	color: $cl-key;
	text-align: center;

	& > .text {
		display: block;
		line-height: 1;
	}

	& > .text.-sub {
		font-size: 1em;
	}

	& > .text.-main {
		font-size: 2.5em;
	}
}
