//dllist
//wrapddlistpadding
.dllist.-upper {
	font-size: 0.9em;
	text-align: left;

	@include mq() {
		font-size: 1em;
	}

	& > .box {
		display: flex;
		flex-flow: row wrap;
		align-items: flex-start;
		justify-content: start;

		& > .title {
			flex: 0 0 8em; //
			padding-top: 0.5em;
			padding-bottom: 0.5em;
		}

		& > .wrap,
		& > .text {
			// flex: 0 1 calc(100% - 8em); //title
			flex: 1 1 auto; //title IE-1
			width: calc(100% - 8em); //IE-2
			padding-left: 2em; //border
			margin-left: auto; //
			border-left: rem(1) solid $cl-black-l;
		}

		& > .text {
			padding-top: 0.5em;
			padding-bottom: 0.5em;
		}
	}
}

//dllist
.dllist.-under {
	font-size: 0.9em;
	text-align: left;

	@include mq() {
		font-size: 1em;
	}

	& > .box {
		display: flex;
		flex-flow: row wrap;
		align-content: center;
		justify-content: start;

		& > .title {
			flex: 1 1 100%; //
			padding-top: 0.5em;
			padding-bottom: 0.5em;
		}

		& > .wrap,
		& > .text {
			flex: 1 1 100%; //
			padding-left: 1em;
		}

		& > .text {
			padding-top: 0.5em;
			padding-bottom: 0.5em;
		}
	}
}

//dllist
//
.dlList01 {
	& > .box {
		display: flex;
		flex-flow: row wrap;
		align-content: center;
		justify-content: start;

		//
		.icon {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			width: 2em;
			color: $cl-key;

			//
			span {
				font-size: 1.2em;
			}
		}

		//dt
		& > .title {
			flex: 1 0 11em; //10emborder
			padding-top: 0.5em;
			padding-bottom: 0.5em;

			@include mq(lg) {
				flex: 0 0 11em; //
			}
		}

		//dd
		//dt
		& > .text {
			flex: 1 1 100%; //
			padding-top: 0.5em;
			padding-bottom: 0.5em;
			padding-left: 2em; //

			@include mq(lg) {
				flex: 1 1 50%; //2
			}
		}

		//dd 
		& > .text.-long {
			flex: 1 1 100%;
		}
	}
}
