//(CSS) ------------------------------

.c-arrow {
	$borderWidth: 3;

	display: block;
	width: 100%;
	height: 100%;
	border-top: rem($borderWidth) solid currentColor;
	border-right: rem($borderWidth) solid currentColor;
	transform: rotate(45deg);

	//
	&.-left {
		transform: rotate(225deg);
	}

	//
	&.-top {
		transform: rotate(-45deg);
	}

	//
	&.-bottom {
		transform: rotate(135deg);
	}
}

//(CSS) ------------------------------

.c-arrowRound {
	$borderWidth: 8;
	$ArrowTipRound: 4;

	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	border-top: rem($borderWidth) solid currentColor;
	border-right: rem($borderWidth) solid currentColor;
	border-top-right-radius: rem($ArrowTipRound); //
	transform: rotate(45deg);

	//
	&::before,
	&::after {
		position: absolute;
		width: rem($borderWidth);
		height: rem($borderWidth);
		content: '';
		background-color: currentColor;
		border-radius: rem(100);
	}

	//
	&::before {
		top: rem(-$borderWidth);
		left: rem($borderWidth / -2);
	}

	//
	&::after {
		right: rem(-$borderWidth);
		bottom: rem($borderWidth / -2);
	}

	//-----------------

	//
	&.-left {
		transform: rotate(225deg);
	}

	//
	&.-top {
		transform: rotate(-45deg);
	}

	//
	&.-bottom {
		transform: rotate(135deg);
	}
}

// point01
.c-pointIcon {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: rem(48);
	height: rem(48);
	color: #fff;
	text-align: center;
	background-color: #00b377;

	@include mq() {
		width: rem(64);
		height: rem(64);
	}

	//point
	&_subText {
		display: block;
		font-size: rem(9);
		line-height: 1;
		letter-spacing: 0.2;
		@include mq() {
			font-size: rem(10);
		}
	}

	//01
	&_mainText {
		display: block;
		font-size: rem(24);
		line-height: 1;
		@include mq() {
			font-size: rem(28);
		}
	}

	//
	&::after {
		position: absolute;
		top: 50%;
		right: rem(-5);
		display: block;
		width: rem(10);
		height: rem(1);
		content: '';
		background-color: $cl-black;
		@include mq() {
			right: rem(-16);
			width: rem(24);
		}
	}
}
